@import url(https://fonts.googleapis.com/css2?family=Source+Code+Pro:wght@400&family=Staatliches&display=swap);
button{-webkit-appearance:none;appearance:none;background-color:#22A0B6;border:none;border-radius:2px;box-shadow:3px 5px 2px rgba(0,0,0,0.18);color:#071E26;cursor:pointer;display:flex;font-size:22px;height:42px;justify-content:center;letter-spacing:2px;line-height:20px;outline:none;padding:12px;text-align:center;transition:background-color 0.1s ease-out}button:hover{background-color:#EB649F}button.square-btn{width:42px}body,*{font-family:'Staatliches', sans-serif}p{font-family:'Source Code Pro', monospace}h1,h2,h3,p{margin:0}h1{font-size:64px;letter-spacing:14px;line-height:64px}h2{font-size:40px;letter-spacing:4px;line-height:40px}h3{font-size:20px;letter-spacing:10px;line-height:20px}*{box-sizing:border-box}body{background-color:#071E26;margin:0}

.App{background:#071E26;background:linear-gradient(200deg, #071E26 0%, rgba(15,82,140,0.06) 100%);display:flex;flex-direction:column;justify-content:space-between;min-height:100vh}header{box-shadow:inset 20vw -20vh 50vh rgba(15,82,140,0.06);background:#EB649F;background:linear-gradient(190deg, #EB649F 0%, #E0447F 100%);box-shadow:5px 8px 2px rgba(0,0,0,0.24);color:#071E26;height:100px;padding:18px;text-align:center}main{display:flex;justify-content:center;min-height:calc(100vh - 120px);width:100%}

.grid{display:flex;flex-direction:column;justify-content:center}.grid .wrapper{background-color:#0F528C;border-radius:2px;box-shadow:5px 8px 2px rgba(0,0,0,0.24);padding:2px}.row{display:flex}.square{align-items:center;background-color:white;border-radius:2px;box-shadow:inset 2px 3px 1px rgba(0,0,0,0.1);display:flex;font-size:30px;height:66px;justify-content:center;line-height:30px;margin:4px;transition:background-color 0.08s ease-in;width:66px}.square.current{background-color:#EB649F}.square.goal{background-color:#CB0C5A}

.modal{align-items:center;background-color:rgba(7,30,38,0.8);display:flex;height:100%;justify-content:center;left:0;position:fixed;top:0;width:100%;z-index:1}.modal.hidden{opacity:0;pointer-events:none;z-index:-1}.modal.hidden .box{-webkit-transform:translateY(20px);transform:translateY(20px)}.modal.hidden .box h2{letter-spacing:3px}.modal.hidden .box *{-webkit-transform:translateY(4px);transform:translateY(4px);opacity:0}.modal .box{align-items:center;background-color:#EB649F;box-shadow:5px 8px 2px rgba(0,0,0,0.24);color:#071E26;display:flex;flex-direction:column;padding:30px 60px;text-align:center;transition:-webkit-transform 0.2s ease;transition:transform 0.2s ease;transition:transform 0.2s ease, -webkit-transform 0.2s ease}.modal .box *{transition:letter-spacing 0.24s ease-in, opacity 0.3s ease, -webkit-transform 0.18s ease;transition:letter-spacing 0.24s ease-in, opacity 0.3s ease, transform 0.18s ease;transition:letter-spacing 0.24s ease-in, opacity 0.3s ease, transform 0.18s ease, -webkit-transform 0.18s ease}.modal .box *:nth-child(1){transition-delay:.1s}.modal .box *:nth-child(2){transition-delay:.14s}.modal .box *:nth-child(3){transition-delay:.18s}.modal .box h2,.modal .box p{margin-bottom:20px}.modal .box button{background-color:#071E26;color:white}.modal .box button:hover{background-color:#0F528C}

section{display:flex;flex-direction:column;justify-content:center;margin-bottom:20px}.text-box{align-items:center;background-color:#0F528C;border-radius:2px;box-shadow:5px 8px 2px rgba(0,0,0,0.24);display:flex;height:36px;justify-content:center;margin-top:30px;text-align:center;width:100%}.text-box h3{color:#fff}.side-bar{position:fixed;right:0}.side-bar button{border-top-right-radius:0;border-bottom-right-radius:0}

